import {
  createRoutes,
  int,
  str,
  template,
  type InferQueryParams,
  render,
  type WithContext,
  type InferPathParams,
} from 'typesafe-routes'

const QUERY_FLASH_MAN_REPO = [str.optional('owner'), str.optional('repo')]
const QUERY_FLASH_MAN_FILE = [...QUERY_FLASH_MAN_REPO, str.optional('path')]

const routeConfig = createRoutes({
  home: {},
  plans: {
    path: ['plans'],
    children: {
      new: {
        path: ['new'],
      },
      detail: {
        path: [int('id')],
      },
      edit: {
        path: [int('id'), 'edit'],
      },
    },
  },
  executions: {
    path: ['executions'],
    children: {
      new: {
        path: ['new'],
        query: [int.optional('planId')],
      },
      detail: {
        path: [int('id')],
      },
      edit: {
        path: [int('id'), 'edit'],
      },
    },
  },
  dashboard: {
    path: ['dashboard'],
  },
  flashMan: {
    path: ['flash-man'],
    children: {
      contents: {
        path: ['contents'],
        query: QUERY_FLASH_MAN_FILE,
      },
      cards: {
        path: ['cards'],
        query: QUERY_FLASH_MAN_FILE,
      },
      learn: {
        path: ['learn'],
        query: QUERY_FLASH_MAN_FILE,
        children: {
          oneFromMany: {
            path: ['one-from-many'],
            query: QUERY_FLASH_MAN_FILE,
          },
          shortAnswer: {
            path: ['short-answer'],
            query: QUERY_FLASH_MAN_FILE,
          },
        },
      },
      stats: {
        path: ['stats'],
      },
    },
  },
})

// (route: R) => string
function makeRouteUtils<R extends WithContext>(route: R) {
  return {
    $config: route,
    $template: () => template(route),
    $path: (path: InferPathParams<R>, query: InferQueryParams<R>) => render(route, { path, query }),
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    $: (query?: InferQueryParams<R> = {}) => render(route, { path: {}, query }),
  }
}

export const routes = {
  home: makeRouteUtils(routeConfig.home),
  plans: {
    ...makeRouteUtils(routeConfig.plans),
    new: makeRouteUtils(routeConfig.plans.new),
    detail: makeRouteUtils(routeConfig.plans.detail),
    edit: makeRouteUtils(routeConfig.plans.edit),
  },
  executions: {
    ...makeRouteUtils(routeConfig.executions),
    new: makeRouteUtils(routeConfig.executions.new),
    detail: makeRouteUtils(routeConfig.executions.detail),
    edit: makeRouteUtils(routeConfig.executions.edit),
  },
  dashboard: makeRouteUtils(routeConfig.dashboard),
  flashMan: {
    ...makeRouteUtils(routeConfig.flashMan),
    dashboard: makeRouteUtils(routeConfig.flashMan.stats),
    contents: makeRouteUtils(routeConfig.flashMan.contents),
    cards: makeRouteUtils(routeConfig.flashMan.cards),
    learn: {
      ...makeRouteUtils(routeConfig.flashMan.learn),
      oneFromMany: makeRouteUtils(routeConfig.flashMan.learn.oneFromMany),
      shortAnswer: makeRouteUtils(routeConfig.flashMan.learn.shortAnswer),
    },
  },
}
