import { createContext, useContext, useEffect, useState, type ReactNode } from 'react'

import type { User, Session } from '@supabase/supabase-js'

import { supabase } from '@/lib/supabase'

interface AuthContextType {
  user: User | null
  session: Session | null
  loading: boolean
  signInWithGitHub: () => Promise<void>
  signOut: () => Promise<void>
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export declare namespace AuthProvider {
  export type Props = {
    userOnly?: boolean
    children: ReactNode
  }
}

export function AuthProvider({ userOnly, children }: AuthProvider.Props) {
  const [user, setUser] = useState<User | null>(null)
  const [session, setSession] = useState<Session | null>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    //   
    if (userOnly) {
      supabase.auth.getUser('mock').then(({ data: { user } }) => {
        // console.log('userOnly:', user)
        setUser(user ?? null)
        setLoading(false)
      })
    } else {
      supabase.auth.getSession().then(({ data: { session } }) => {
        // console.log(' :', session)
        setSession(session)
        setUser(session?.user ?? null)
        setLoading(false)
      })
    }

    //    
    const {
      data: { subscription },
    } = supabase.auth.onAuthStateChange((_event, session) => {
      if (userOnly) {
        return
      }
      // console.log('onAuthStateChange:', _event, session?.provider_token, session)

      setSession(session)
      setUser(session?.user ?? null)
      setLoading(false)
    })

    return () => subscription.unsubscribe()
  }, [userOnly])

  async function signInWithGitHub() {
    // const redirectUrl = import.meta.env.PROD
    //   ? 'https://training-buddy.github.io'
    //   : window.location.origin
    const redirectUrl = window.location.origin

    // console.log({ redirectUrl })
    const { error } = await supabase.auth.signInWithOAuth({
      provider: 'github',
      options: {
        redirectTo: redirectUrl,
        scopes: 'repo read:org',
      },
    })
    if (error) {
      // eslint-disable-next-line no-console
      console.error('GitHub  :', error.message)
      throw error
    }
  }

  async function signOut() {
    const { error } = await supabase.auth.signOut()
    if (error) {
      // eslint-disable-next-line no-console
      console.error(' :', error.message)
      throw error
    }
  }

  return (
    <AuthContext.Provider value={{ user, session, loading, signInWithGitHub, signOut }}>
      {children}
    </AuthContext.Provider>
  )
}

// eslint-disable-next-line react-refresh/only-export-components
export function useAuth() {
  const context = useContext(AuthContext)
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider')
  }
  return context
}
