import { useState } from 'react'

import { Logout as LogoutIcon } from '@mui/icons-material'
import { Avatar, Box, IconButton, Menu, MenuItem, Typography, Divider } from '@mui/material'

import { useAuth } from '../contexts/AuthContext'

export function UserMenu() {
  const { user, signOut } = useAuth()
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null)

  if (!user) return null

  const handleOpen = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget)
  }

  const handleClose = () => {
    setAnchorEl(null)
  }

  const handleSignOut = async () => {
    handleClose()
    await signOut()
  }

  const avatarUrl = user.user_metadata?.avatar_url
  const displayName = user.user_metadata?.full_name || user.user_metadata?.user_name || user.email

  return (
    <Box>
      <IconButton onClick={handleOpen} size="small">
        <Avatar src={avatarUrl} alt={displayName} sx={{ width: 32, height: 32 }}>
          {displayName?.[0]?.toUpperCase()}
        </Avatar>
      </IconButton>
      <Menu
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleClose}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
        transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      >
        <Box sx={{ px: 2, py: 1 }}>
          <Typography variant="subtitle2">{displayName}</Typography>
          <Typography variant="body2" color="text.secondary">
            {user.email}
          </Typography>
        </Box>
        <Divider />
        <MenuItem onClick={handleSignOut}>
          <LogoutIcon fontSize="small" sx={{ mr: 1 }} />
          
        </MenuItem>
      </Menu>
    </Box>
  )
}
