import * as React from 'react'

import { Routes, Route, Navigate } from 'react-router-dom'

import { AppContextProvider, FullScreen } from './components/AppContext'
import { AppLayout } from './components/AppLayout'
import { AuthProvider, useAuth } from './contexts/AuthContext'
import { routes } from './routes'

const HomePage = React.lazy(() => import('./pages/home').then((mod) => ({ default: mod.HomePage })))
const LoginPage = React.lazy(() =>
  import('./pages/login').then((mod) => ({ default: mod.LoginPage }))
)
const DashboardPage = React.lazy(() =>
  import('./pages/dashboard').then((mod) => ({ default: mod.DashboardPage }))
)
const FlashManPage = React.lazy(() =>
  import('./pages/flash-man').then((mod) => ({ default: mod.FlashManPage }))
)
const ContentsPage = React.lazy(() =>
  import('./pages/flash-man/contents').then((mod) => ({ default: mod.ContentsPage }))
)
const CardsPage = React.lazy(() =>
  import('./pages/flash-man/cards').then((mod) => ({ default: mod.CardsPage }))
)
const LearningLayout = React.lazy(() => import('./pages/flash-man/learn/LearningLayout'))
const LearnPage = React.lazy(() =>
  import('./pages/flash-man/learn').then((mod) => ({ default: mod.LearnPage }))
)
const OneFromManyPage = React.lazy(() =>
  import('./pages/flash-man/learn/one-from-many').then((mod) => ({ default: mod.OneFromManyPage }))
)
const ShortAnswerPage = React.lazy(() =>
  import('./pages/flash-man/learn/short-answer').then((mod) => ({ default: mod.ShortAnswerPage }))
)
const FlashManStatsPage = React.lazy(() =>
  import('./pages/flash-man/stats').then((mod) => ({ default: mod.FlashManStatsPage }))
)

const PlansPage = React.lazy(() =>
  import('./pages/plans').then((mod) => ({ default: mod.PlansPage }))
)
const PlansNewPage = React.lazy(() =>
  import('./pages/plans/new').then((mod) => ({ default: mod.PlansNewPage }))
)
const PlansDetailPage = React.lazy(() =>
  import('./pages/plans/detail').then((mod) => ({ default: mod.PlansDetailPage }))
)
const PlansEditPage = React.lazy(() =>
  import('./pages/plans/edit').then((mod) => ({ default: mod.PlansEditPage }))
)
const ExecutionsPage = React.lazy(() =>
  import('./pages/executions').then((mod) => ({ default: mod.ExecutionsPage }))
)
const ExecutionsNewPage = React.lazy(() =>
  import('./pages/executions/new').then((mod) => ({ default: mod.ExecutionsNewPage }))
)
const ExecutionsDetailPage = React.lazy(() =>
  import('./pages/executions/detail').then((mod) => ({ default: mod.ExecutionsDetailPage }))
)
const ExecutionsEditPage = React.lazy(() =>
  import('./pages/executions/edit').then((mod) => ({ default: mod.ExecutionsEditPage }))
)

function ProtectedRoute({ children }: { children: React.ReactNode }) {
  const { user, loading } = useAuth()

  if (loading) {
    return null //   
  }

  if (!user) {
    return <Navigate to="/login" replace />
  }

  return <>{children}</>
}

export type Props = Pick<AuthProvider.Props, 'userOnly'>

function App(props: Props) {
  return (
    <AuthProvider {...props}>
      <AppContextProvider>
        <AppLayout>
          <Routes>
            <Route path={routes.home.$template()} element={<HomePage />} />
            <Route path="/login" element={<LoginPage />} />

            {/*   */}
            <Route
              path={routes.plans.$template()}
              element={
                <ProtectedRoute>
                  <PlansPage />
                </ProtectedRoute>
              }
            >
              <Route path={routes.plans.new.$template()} element={<PlansNewPage />} />
              <Route path={routes.plans.detail.$template()} element={<PlansDetailPage />} />
              <Route path={routes.plans.edit.$template()} element={<PlansEditPage />} />
            </Route>

            <Route
              path={routes.executions.$template()}
              element={
                <ProtectedRoute>
                  <ExecutionsPage />
                </ProtectedRoute>
              }
            >
              <Route path={routes.executions.new.$template()} element={<ExecutionsNewPage />} />
              <Route
                path={routes.executions.detail.$template()}
                element={<ExecutionsDetailPage />}
              />
              <Route path={routes.executions.edit.$template()} element={<ExecutionsEditPage />} />
            </Route>

            <Route
              path={routes.dashboard.$template()}
              element={
                <ProtectedRoute>
                  <DashboardPage />
                </ProtectedRoute>
              }
            />

            <Route
              path={routes.flashMan.$template()}
              element={
                <ProtectedRoute>
                  <FlashManPage />
                </ProtectedRoute>
              }
            >
              <Route path={routes.flashMan.contents.$template()} element={<ContentsPage />} />
              <Route path={routes.flashMan.cards.$template()} element={<CardsPage />} />
              <Route
                path={routes.flashMan.learn.$template()}
                element={
                  <FullScreen>
                    <LearningLayout />
                  </FullScreen>
                }
              >
                <Route index element={<LearnPage />} />
                <Route
                  path={routes.flashMan.learn.oneFromMany.$template()}
                  element={<OneFromManyPage />}
                />
                <Route
                  path={routes.flashMan.learn.shortAnswer.$template()}
                  element={<ShortAnswerPage />}
                />
              </Route>
              <Route path={routes.flashMan.dashboard.$template()} element={<FlashManStatsPage />} />
            </Route>
          </Routes>
        </AppLayout>
      </AppContextProvider>
    </AuthProvider>
  )
}

export default App
