import { createContext, useContext, useEffect, useState } from 'react'

function useAppContextValue() {
  const [fullscreen, setFullscreen] = useState(false)
  return {
    fullscreen,
    setFullscreen,
  }
}

export type AppContext = ReturnType<typeof useAppContextValue>

// eslint-disable-next-line @typescript-eslint/no-explicit-any
const Context = createContext<AppContext>({} as any)

export function AppContextProvider({ children }: { children: React.ReactNode }) {
  const value = useAppContextValue()

  return <Context.Provider value={value}>{children}</Context.Provider>
}

// eslint-disable-next-line react-refresh/only-export-components
export function useAppContext() {
  return useContext(Context)
}

export function FullScreen({ children }: { children: React.ReactNode }) {
  const { fullscreen, setFullscreen } = useAppContext()
  useEffect(() => {
    setFullscreen(true)
    return () => {
      setFullscreen(false)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])
  return fullscreen ? <>{children}</> : null
}
