import { useState, useEffect, useCallback, useRef } from 'react'

import { useSetAtom } from 'jotai'

import { timerStateAtom } from '@/atoms/timerState'

const TIMER_STORAGE_KEY = 'training-timer-state'
const NOTIFICATION_TAG = 'training-timer-notification'

async function requestNotificationPermission(): Promise<boolean> {
  if (!('Notification' in window)) {
    return false
  }
  if (Notification.permission === 'granted') {
    return true
  }
  if (Notification.permission !== 'denied') {
    const permission = await Notification.requestPermission()
    return permission === 'granted'
  }
  return false
}

async function showTimerNotification(elapsedSeconds: number): Promise<void> {
  if (!('Notification' in window) || Notification.permission !== 'granted') {
    return
  }

  const hours = Math.floor(elapsedSeconds / 3600)
  const minutes = Math.floor((elapsedSeconds % 3600) / 60)
  const secs = elapsedSeconds % 60

  let timeStr: string
  if (hours > 0) {
    timeStr = `${hours}:${minutes.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
  } else {
    timeStr = `${minutes}:${secs.toString().padStart(2, '0')}`
  }

  // Use ServiceWorkerRegistration.showNotification for PWA compatibility
  if ('serviceWorker' in navigator) {
    try {
      const registration = await navigator.serviceWorker.ready
      await registration.showNotification(' Training Timer Running', {
        body: `Elapsed: ${timeStr}`,
        tag: NOTIFICATION_TAG,
        silent: true,
        requireInteraction: true,
      })
    } catch {
      // Fallback: silently fail if service worker notification fails
    }
  }
}

async function closeTimerNotification(): Promise<void> {
  if ('serviceWorker' in navigator) {
    try {
      const registration = await navigator.serviceWorker.ready
      const notifications = await registration.getNotifications({ tag: NOTIFICATION_TAG })
      notifications.forEach((n) => n.close())
    } catch {
      // Silently fail
    }
  }
}

interface TimerState {
  startedAt: string | null
  pausedAt: string | null
  lastActiveAt: string | null // Timestamp when state was last saved while running
  accumulatedSeconds: number
  isRunning: boolean
  planId: number | null
  unitCount: number | null
}

interface UseTimerOptions {
  planId?: number | null
  unitCount?: number | null
}

interface UseTimerReturn {
  elapsedSeconds: number
  isRunning: boolean
  isPaused: boolean
  start: (options?: UseTimerOptions) => void
  pause: () => void
  resume: () => void
  reset: () => void
  startedAt: Date | null
  planId: number | null
  unitCount: number | null
  setUnitCount: (count: number) => void
}

function loadTimerState(): TimerState | null {
  try {
    const saved = localStorage.getItem(TIMER_STORAGE_KEY)
    if (saved) {
      return JSON.parse(saved)
    }
  } catch {
    // Ignore parse errors
  }
  return null
}

function saveTimerState(state: TimerState) {
  try {
    localStorage.setItem(TIMER_STORAGE_KEY, JSON.stringify(state))
  } catch {
    // Ignore storage errors
  }
}

function clearTimerState() {
  try {
    localStorage.removeItem(TIMER_STORAGE_KEY)
  } catch {
    // Ignore storage errors
  }
}

export function useTimer(): UseTimerReturn {
  const [startedAt, setStartedAt] = useState<Date | null>(null)
  const [resumedAt, setResumedAt] = useState<Date | null>(null)
  const [accumulatedSeconds, setAccumulatedSeconds] = useState(0)
  const [isRunning, setIsRunning] = useState(false)
  const [isPaused, setIsPaused] = useState(false)
  const [elapsedSeconds, setElapsedSeconds] = useState(0)
  const [planId, setPlanId] = useState<number | null>(null)
  const [unitCount, setUnitCountState] = useState<number | null>(null)
  const intervalRef = useRef<number | null>(null)
  const setGlobalTimerState = useSetAtom(timerStateAtom)

  // Sync global timer state for application-level indication
  useEffect(() => {
    setGlobalTimerState({
      isRunning,
      isPaused,
      elapsedSeconds,
      planId,
    })
  }, [isRunning, isPaused, elapsedSeconds, planId, setGlobalTimerState])

  // Calculate current elapsed time based on timestamps
  const calculateElapsed = useCallback(() => {
    if (!isRunning || !resumedAt) {
      return accumulatedSeconds
    }
    const now = Date.now()
    const currentSessionSeconds = Math.floor((now - resumedAt.getTime()) / 1000)
    return accumulatedSeconds + currentSessionSeconds
  }, [isRunning, resumedAt, accumulatedSeconds])

  // Restore timer state on mount
  useEffect(() => {
    const saved = loadTimerState()
    if (saved && saved.startedAt) {
      const startDate = new Date(saved.startedAt)
      // eslint-disable-next-line react-hooks/set-state-in-effect
      setStartedAt(startDate)
      setAccumulatedSeconds(saved.accumulatedSeconds)
      setPlanId(saved.planId ?? null)
      setUnitCountState(saved.unitCount ?? null)

      if (saved.isRunning && saved.lastActiveAt) {
        // Timer was running - calculate elapsed time since it was last saved
        const additionalSeconds = Math.floor(
          (Date.now() - new Date(saved.lastActiveAt).getTime()) / 1000
        )

        setResumedAt(new Date())
        setAccumulatedSeconds(saved.accumulatedSeconds + additionalSeconds)
        setIsRunning(true)
        setIsPaused(false)
      } else if (saved.pausedAt) {
        // Timer was paused
        setIsRunning(false)
        setIsPaused(true)
      }
    }
  }, [])

  // Save state whenever it changes
  useEffect(() => {
    if (startedAt) {
      const now = new Date().toISOString()
      const state: TimerState = {
        startedAt: startedAt.toISOString(),
        pausedAt: isRunning ? null : now,
        lastActiveAt: isRunning ? now : null, // Track when we last saved while running
        accumulatedSeconds: isRunning ? calculateElapsed() : calculateElapsed(),
        isRunning,
        planId,
        unitCount,
      }
      saveTimerState(state)
    }
  }, [startedAt, isRunning, accumulatedSeconds, calculateElapsed, planId, unitCount])

  const clearTimer = useCallback(() => {
    if (intervalRef.current) {
      clearInterval(intervalRef.current)
      intervalRef.current = null
    }
  }, [])

  const closeNotification = useCallback(() => {
    closeTimerNotification()
  }, [])

  const start = useCallback(async (options?: UseTimerOptions) => {
    await requestNotificationPermission()
    const now = new Date()
    setStartedAt(now)
    setResumedAt(now)
    setAccumulatedSeconds(0)
    setElapsedSeconds(0)
    setIsRunning(true)
    setIsPaused(false)
    if (options?.planId !== undefined) {
      setPlanId(options.planId)
    }
    if (options?.unitCount !== undefined) {
      setUnitCountState(options.unitCount)
    }
  }, [])

  const pause = useCallback(() => {
    // Save accumulated time before pausing
    setAccumulatedSeconds(calculateElapsed())
    setIsRunning(false)
    setIsPaused(true)
    setResumedAt(null)
    clearTimer()
    closeNotification()
  }, [clearTimer, closeNotification, calculateElapsed])

  const resume = useCallback(async () => {
    await requestNotificationPermission()
    setResumedAt(new Date())
    setIsRunning(true)
    setIsPaused(false)
  }, [])

  const reset = useCallback(() => {
    setElapsedSeconds(0)
    setAccumulatedSeconds(0)
    setIsRunning(false)
    setIsPaused(false)
    setStartedAt(null)
    setResumedAt(null)
    setPlanId(null)
    setUnitCountState(null)
    clearTimer()
    closeNotification()
    clearTimerState()
  }, [clearTimer, closeNotification])

  // Update elapsed seconds using interval (for UI updates)
  useEffect(() => {
    if (isRunning) {
      // Update immediately
      // eslint-disable-next-line react-hooks/set-state-in-effect
      setElapsedSeconds(calculateElapsed())

      intervalRef.current = window.setInterval(() => {
        setElapsedSeconds(calculateElapsed())
      }, 1000)
    } else {
      clearTimer()
      setElapsedSeconds(calculateElapsed())
    }

    return clearTimer
  }, [isRunning, clearTimer, calculateElapsed])

  // Show/update notification when timer is running
  useEffect(() => {
    if (isRunning) {
      // Update notification every 10 seconds to avoid too many updates
      const shouldUpdate = elapsedSeconds % 10 === 0 || elapsedSeconds <= 1
      if (shouldUpdate) {
        showTimerNotification(elapsedSeconds)
      }
    }
  }, [isRunning, elapsedSeconds])

  // Cleanup notification on unmount
  useEffect(() => {
    return () => {
      closeTimerNotification()
    }
  }, [])

  // Handle visibility change - recalculate elapsed time when app becomes visible
  useEffect(() => {
    function handleVisibilityChange() {
      if (document.visibilityState === 'visible' && isRunning) {
        // Recalculate elapsed time when app becomes visible
        setElapsedSeconds(calculateElapsed())
      }
    }

    document.addEventListener('visibilitychange', handleVisibilityChange)
    return () => {
      document.removeEventListener('visibilitychange', handleVisibilityChange)
    }
  }, [isRunning, calculateElapsed])

  const setUnitCount = useCallback((count: number) => {
    setUnitCountState(count)
  }, [])

  return {
    elapsedSeconds,
    isRunning,
    isPaused,
    start,
    pause,
    resume,
    reset,
    startedAt,
    planId,
    unitCount,
    setUnitCount,
  }
}

export function formatTime(seconds: number): string {
  const hours = Math.floor(seconds / 3600)
  const minutes = Math.floor((seconds % 3600) / 60)
  const secs = seconds % 60

  if (hours > 0) {
    return `${hours}:${minutes.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
  }
  return `${minutes}:${secs.toString().padStart(2, '0')}`
}

export function formatDuration(seconds: number): string {
  const hours = Math.floor(seconds / 3600)
  const minutes = Math.floor((seconds % 3600) / 60)
  const secs = seconds % 60

  const parts: string[] = []
  if (hours > 0) parts.push(`${hours}`)
  if (minutes > 0) parts.push(`${minutes}`)
  if (secs > 0 || parts.length === 0) parts.push(`${secs}`)

  return parts.join(' ')
}
