import { type ReactNode } from 'react'

import { useAtom, useAtomValue } from 'jotai'
import { Link, useLocation } from 'react-router-dom'

import {
  CalendarToday,
  DarkMode,
  Dashboard,
  FitnessCenter,
  LightMode,
  School,
  Timer as TimerIcon,
} from '@mui/icons-material'
import {
  AppBar,
  BottomNavigation,
  BottomNavigationAction,
  Box,
  Button,
  Chip,
  CircularProgress,
  IconButton,
  Paper,
  Toolbar,
  Typography,
  keyframes,
} from '@mui/material'

import { useIsMobile } from '@/hooks/useIsMobile'

import { darkModeAtom } from '../atoms/darkMode'
import { timerStateAtom } from '../atoms/timerState'
import { useAuth } from '../contexts/AuthContext'
import { formatTime } from '../hooks/useTimer'
import { routes } from '../routes'

import { useAppContext } from './AppContext'
import { LoginButton } from './LoginButton'
import { UserMenu } from './UserMenu'

const pulse = keyframes`
  0% { opacity: 1; }
  50% { opacity: 0.5; }
  100% { opacity: 1; }
`

interface AppLayoutProps {
  children: ReactNode
}

export function AppLayout({ children }: AppLayoutProps) {
  const { user, loading } = useAuth()
  const [darkMode, setDarkMode] = useAtom(darkModeAtom)
  const timerState = useAtomValue(timerStateAtom)
  const location = useLocation()
  const { isMobile } = useIsMobile()
  const isActive = (path: string) => location.pathname.startsWith(path)
  const isTimerActive = timerState.isRunning || timerState.isPaused

  const { fullscreen } = useAppContext()

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
      {fullscreen ? (
        children
      ) : (
        <>
          <AppBar position="static">
            <Toolbar>
              <Typography
                variant="h6"
                component={Link}
                to={routes.home.$()}
                sx={{
                  flexGrow: 1,
                  textDecoration: 'none',
                  color: 'inherit',
                }}
              >
                v{import.meta.env.VITE_APP_VERSION || '0.0.0'}
              </Typography>

              {user && !isMobile && (
                <Box sx={{ display: 'flex', gap: 1, mr: 2 }}>
                  <Button
                    component={Link}
                    to={routes.plans.$()}
                    color="inherit"
                    size="small"
                    sx={{
                      backgroundColor: isActive(routes.plans.$())
                        ? 'rgba(255,255,255,0.2)'
                        : 'transparent',
                    }}
                  >
                    
                  </Button>
                  <Button
                    component={Link}
                    to={routes.executions.$()}
                    color="inherit"
                    size="small"
                    sx={{
                      backgroundColor: isActive(routes.executions.$())
                        ? 'rgba(255,255,255,0.2)'
                        : 'transparent',
                    }}
                  >
                    
                  </Button>
                  <Button
                    component={Link}
                    to={routes.dashboard.$()}
                    color="inherit"
                    size="small"
                    sx={{
                      backgroundColor: isActive(routes.dashboard.$())
                        ? 'rgba(255,255,255,0.2)'
                        : 'transparent',
                    }}
                  >
                    
                  </Button>
                  <Button
                    component={Link}
                    to={routes.flashMan.dashboard.$()}
                    color="inherit"
                    size="small"
                    sx={{
                      backgroundColor: isActive(routes.flashMan.$())
                        ? 'rgba(255,255,255,0.2)'
                        : 'transparent',
                    }}
                  >
                    FlashMan
                  </Button>
                </Box>
              )}

              {isTimerActive && (
                <Chip
                  component={Link}
                  to={routes.executions.new.$()}
                  icon={<TimerIcon />}
                  label={formatTime(timerState.elapsedSeconds)}
                  color={timerState.isRunning ? 'error' : 'warning'}
                  size="small"
                  clickable
                  sx={{
                    mr: 1,
                    fontWeight: 'bold',
                    fontFamily: 'monospace',
                    animation: timerState.isRunning ? `${pulse} 2s ease-in-out infinite` : 'none',
                  }}
                />
              )}

              <IconButton color="inherit" onClick={() => setDarkMode(!darkMode)} sx={{ mr: 1 }}>
                {darkMode ? <LightMode /> : <DarkMode />}
              </IconButton>

              {loading ? (
                <CircularProgress size={24} color="inherit" />
              ) : user ? (
                <UserMenu />
              ) : (
                <LoginButton />
              )}
            </Toolbar>
          </AppBar>

          <Box component="main" sx={{ flexGrow: 1, pb: user && isMobile ? 7 : 0 }}>
            {children}
          </Box>

          {user && isMobile && (
            <Paper sx={{ position: 'fixed', bottom: 0, left: 0, right: 0 }} elevation={3}>
              <BottomNavigation
                showLabels
                value={
                  isActive(routes.plans.$())
                    ? 0
                    : isActive(routes.executions.$())
                      ? 1
                      : isActive(routes.dashboard.$())
                        ? 2
                        : isActive(routes.flashMan.$())
                          ? 3
                          : -1
                }
              >
                <BottomNavigationAction
                  component={Link}
                  to={routes.plans.$()}
                  label=""
                  icon={<CalendarToday />}
                />
                <BottomNavigationAction
                  component={Link}
                  to={routes.executions.$()}
                  label=""
                  icon={<FitnessCenter />}
                />
                <BottomNavigationAction
                  component={Link}
                  to={routes.dashboard.$()}
                  label=""
                  icon={<Dashboard />}
                />
                <BottomNavigationAction
                  component={Link}
                  to={routes.flashMan.dashboard.$()}
                  label="Flashman"
                  icon={<School />}
                />
              </BottomNavigation>
            </Paper>
          )}
        </>
      )}
    </Box>
  )
}
